<script>
// @ is an alias to /src

export default {
  name: 'Home',
  data() {
    return{
      city: "",
      className:[]
    }
  },
  methods:{
    rate(e){
//récupère l'élément au survole de la souris
      const selectedStar =e.target

//split pour récupérer ensuite le numéro de l'étoile
      const starClassNumber = selectedStar.className.split('r')
//boucle for qui se limite aux numéro de l'étoile survolée
     for (let i = 1; i < parseInt(starClassNumber[1])+1 ; i++){
      // récupère l'id de l'étoile
       const starSameId = selectedStar.id.split('star')[0]
      // si erreur car l'étoile n'a pas la class star"i" console.log
         if((document.querySelector(`#${starSameId}star${i}`).className == `star${i}`)==undefined){
          console.log(`undefind${i}`)
        //sinon ajoute yellow_star"i" dans la liste de class de l'élément
        }else{
        document.querySelector(`#${starSameId}star${i}`).classList.add( `yellow_star${i}`)
        }
    
      } 

    },
    unrate(e){

//récupère l'élément lors du départ de la souris
      const selectedStar =e.target
//split pour récupérer ensuite le numéro de l'étoile      
      const starClassNumber = selectedStar.className.split('r')

//boucle for qui se limite aux numéro de l'étoile survolée
     for (let i = 1; i < parseInt(starClassNumber[2])+1 ; i++){
      // récupère l'id de l'étoile
       const starSameId = selectedStar.id.split('star')[0]
      // si erreur car l'étoile n'a pas la class star"i" console.log
         if((document.querySelector(`#${starSameId}star${i}`).className == `yellow_star${i}`) == undefined){
          console.log(`undefind${i}`)
      //sinon retire yellow_star"i" dans la liste de class de l'élément
        }else{
        document.querySelector(`#${starSameId}star${i}`).classList.remove( `yellow_star${i}`)
        }
        
    
      } 

    },
  },
  mounted(){
    this.$store.dispatch("sayHello")
  }
   
}
</script>

